<form class="row row-cols-lg-auto g-3 align-items-center" @submit.prevent="onSubmit" method="post"><div class="col-12"><label class="visually-hidden" for="inputSymbol">Symbol</label><input class="form-control" id="inputSymbol" v-model="symbol" type="text" placeholder="BTC" required></div><div class="col-12"><label class="visually-hidden" for="inputAmount">Amount</label><input class="form-control text-end" id="inputAmount" v-model="amount" type="text" placeholder="0.00000000" required></div><div class="col-12"><label class="visually-hidden" for="inputExchange">Exchange</label><select class="form-select" id="inputExchange" v-model="exchange" @change="onExchangeChange"><option v-for="value in exchanges" :key="value.id" :value="value.id" :selected="exchange === value.id">{{ value.name }}</option></select></div><div class="col-12"><button class="btn btn-primary" type="submit" :disabled="loading._">Add your asset</button></div></form>