<add-form class="mb-3" v-if="adding" @add="onAdd"></add-form><div class="table-responsive"><table class="table align-middle"><thead><tr><th class="text-nowrap text-center" colspan="4"><div class="btn-group" role="group" aria-label="Actions"><button class="btn btn-sm border-0" type="button" @click="onAddClick" title="Add" data-bs-toggle="tooltip"><i class="fas fa-plus-circle"></i></button><button class="btn btn-sm border-0" type="button" @click="onProtectClick" title="Protect" data-bs-toggle="tooltip"><i class="fas" :class="protected ? 'fa-eye-slash' : 'fa-eye'"></i></button><button class="btn btn-sm border-0" type="button" @click="onExportClick" title="Export" data-bs-toggle="tooltip"><i class="fas fa-file-export"></i></button><button class="btn btn-sm border-0" type="button" @click="onImportClick" title="Import" data-bs-toggle="tooltip"><i class="fas fa-file-import"></i></button></div></th><th class="text-nowrap text-end"><span class="text-primary">Initial (USD)</span></th><th class="text-nowrap text-end"><span :class="{'text-danger': profit &lt; 0, 'text-success': profit &gt; 0, 'text-light': profit === 0}" @click="onProfitClick">Profit (USD)</span></th><th class="text-nowrap text-end"><span class="text-info">Current (USD)</span></th></tr></thead><tbody><tr><td colspan="4"></td><td class="text-end"><protected-formatted-number-input v-model="initial" :fractionDigits="-1" textClass="text-primary" inputClass="outline-0 border-0 w-100 text-primary text-end" :protected="protected"></protected-formatted-number-input></td><td class="text-end"><protected-formatted-number :class="{'text-danger': profit &lt; 0, 'text-success': profit &gt; 0, 'text-light': profit === 0}" :value="profit" :protected="unprotectedProfit ? false : protected"></protected-formatted-number></td><td class="text-end"><protected-formatted-number class="text-info" :value="current" :protected="protected"></protected-formatted-number></td></tr></tbody><thead><tr><th class="text-nowrap text-center">#</th><th class="text-nowrap"></th><th class="text-nowrap">Coin/Token</th><th class="text-nowrap text-end w180">Price (USD)</th><th class="text-nowrap text-end w180">Amount</th><th class="text-nowrap text-end w180" @click="onCurrentSortClick">Current (USD)<i class="fas ms-2" :class="{'fa-sort text-light': sortCurrent === 0, 'fa-sort-up': sortCurrent === 1, 'fa-sort-down': sortCurrent === 2}"></i></th><th class="text-nowrap text-end w180">% Current</th></tr></thead><tbody><tr v-for="(asset, index) in assets"><td class="text-center">{{ index + 1 }}</td><td class="text-center text-nowrap"><div class="btn-group" role="group" aria-label="Item actions"><button class="btn btn-sm border-0" :disabled="loading._" @click="onDeleteClick(asset, index)"><i class="fas fa-times"></i></button><button class="btn btn-sm border-0" :disabled="loading._ || index === 0" @click="onMoveUpClick(asset, index)"><i class="fas fa-arrow-up"></i></button><button class="btn btn-sm border-0" :disabled="loading._ || index === assets.length - 1" @click="onMoveDownClick(asset, index)"><i class="fas fa-arrow-down"></i></button></div></td><th><a class="btn btn-link btn-sm" :class="{disabled: !asset.chartUrl}" :href="asset.chartUrl ? asset.chartUrl : '#'" target="_blank"><i class="fas fa-chart-area"></i></a>{{ asset.symbol }}</th><td class="text-end"><price :asset="asset" :protected="protected" @update="onPriceUpdate(asset, index, $event)"></price></td><td class="text-end"><protected-formatted-number-input :modelValue="asset.amount" @update:modelValue="onAmountUpdate(asset, index, $event)" :fractionDigits="-1" inputClass="outline-0 border-0 w-100 h-100 text-end" :protected="protected"></protected-formatted-number-input></td><td class="text-end"><protected-formatted-number :value="asset.price * asset.amount" :protected="protected"></protected-formatted-number></td><td class="text-end"><protected-formatted-number :value="current ? asset.price * asset.amount / current * 100 : 0" :protected="protected"></protected-formatted-number></td></tr></tbody></table></div>